import React from "react";
import EducationSectionStyle from "./style";

export default function EducationSection({ content }) {
    return (
        <EducationSectionStyle>
            {/* <img className="img-company" src="https://fakeimg.pl/50x50/" /> */}

            <div className="content">
                <h2 className="course">{content.course}</h2>
                <h3 className="institution">{content.institution}</h3>
                <span className="working-time">{content.startYear} - {content.endYear}</span>
                {
                    content.activities.length ? (
                        <ul className="activities">
                            {content.activities.map((activite, index) => (
                                <li key={index} className="activite"><strong className="title-activite">{activite.title}: </strong>{activite.description}</li>
                            ))}
                        </ul>
                    ) : (<></>)
                }
            </div>
        </EducationSectionStyle>
    );
}